import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Text, TextTag } from "components/old/Text";
import {
    Heading,
    HeadingTag,
    HeadingTheme,
} from "common/components/new/Heading";

import { MobileBlockProps } from "./MobileBlock.types";
import styles from "./MobileBlock.module.scss";

const cx = classNames.bind(styles);

const MobileBlock: FunctionComponent<MobileBlockProps> = (props) => {
    const { title, list, className, description } = props;

    return (
        <section className={cx("root", className)}>
            <div className={cx("row")}>
                <div className={cx("column", "column_content")}>
                    <Heading
                        tag={HeadingTag.H2}
                        theme={HeadingTheme.Red}
                        className={cx("title")}
                    >
                        {title}
                    </Heading>
                    <Text className={cx("description")}>{description}</Text>
                </div>
                <div className={cx("column", "column_list")}>
                    {list.map((item: any, index: number) => {
                        return (
                            <div key={index} className="item">
                                <Text
                                    tag={TextTag.H2}
                                    className={cx("item_title")}
                                >
                                    {item.title}
                                </Text>
                            </div>
                        );
                    })}
                </div>
            </div>
        </section>
    );
};

export default MobileBlock;
