import { FunctionComponent, useRef, useState, useEffect } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingTheme,
} from "common/components/new/Heading";
import { Text } from "components/old/Text";

import { SliderProps } from "./Slider.types";
import styles from "./Slider.module.scss";

const cx = classNames.bind(styles);

const DELAY = 4000;

const Slider: FunctionComponent<SliderProps> = (props) => {
    const { title, list = [], className, description } = props;

    const [activeItem, setActiveItem] = useState(0);
    const [heightActiveItem, setHeightActiveItem] = useState(0);
    const [offsetTopBackground, setOffsetTopBackground] = useState(0);
    const listBlock = useRef<HTMLDivElement>(null);
    const itemBlock = useRef<HTMLDivElement>(null);

    const openSlide = (index: number) => {
        if (index !== activeItem) {
            setActiveItem(index);
        }
    };

    useEffect(() => {
        const itemBlocks =
            (listBlock &&
                listBlock.current &&
                Array.from(
                    listBlock.current.querySelectorAll(".item_wrapp")
                )) ||
            [];

        const height =
            itemBlocks[activeItem] && itemBlocks[activeItem].clientHeight;

        const blocks =
            (listBlock &&
                listBlock.current &&
                Array.from(listBlock.current.querySelectorAll(".item"))) ||
            [];

        const topOffset = blocks.reduce((acc: number, block, index) => {
            if (index < activeItem) {
                acc = acc + block.clientHeight;
            }

            return acc;
        }, 0);

        setHeightActiveItem(height);
        setOffsetTopBackground(topOffset);

        const timer = setInterval(() => {
            if (activeItem + 1 < blocks.length) {
                setActiveItem(activeItem + 1);
            } else {
                setActiveItem(0);
            }
        }, DELAY);

        return () => clearInterval(timer);
    }, [activeItem]);

    return (
        <section className={cx("root", className)}>
            <div className={cx("row")}>
                <div className={cx("column", "column_content")}>
                    <Heading
                        tag={HeadingTag.H2}
                        theme={HeadingTheme.Red}
                        className={cx("title")}
                    >
                        {title}
                    </Heading>
                    <Text className={cx("description")}>{description}</Text>
                    <div className={cx("list")} ref={listBlock}>
                        <div
                            style={{
                                top: `${offsetTopBackground}px`,
                                height: `${heightActiveItem}px`,
                            }}
                            className={cx("item-background")}
                        />
                        {list.map((item: any, index: number) => {
                            const active = index === activeItem;

                            return (
                                <div key={index} className="item">
                                    <div className={cx("item_wrapp")}>
                                        <div className={"item_wrapp"}>
                                            <div
                                                className={cx(
                                                    "item",
                                                    active && "item_active"
                                                )}
                                                onClick={() => openSlide(index)}
                                                ref={itemBlock}
                                            >
                                                <div
                                                    className={cx(
                                                        "point",
                                                        active && "point_active"
                                                    )}
                                                />
                                                <Text
                                                    className={cx("item_title")}
                                                >
                                                    {item.title}
                                                </Text>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
                <div className={cx("column", "column_background")}>
                    {list.map((item: any, index: number) => {
                        const active = index === activeItem;

                        return (
                            <div
                                key={item.img}
                                className={cx(
                                    "background",
                                    active
                                        ? "background_show"
                                        : "background_hide"
                                )}
                                style={{
                                    backgroundImage: `url(${item.img})`,
                                }}
                            />
                        );
                    })}
                </div>
            </div>
        </section>
    );
};

export default Slider;
