import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { StepsListProps } from "components/StepsList";

import { StepsListItem } from "./components/StepsListItem";

import styles from "./StepsList.module.scss";

const cx = classNames.bind(styles);

const StepsList: FunctionComponent<StepsListProps> = (props) => {
    const { title, list, className } = props;

    return (
        <div className={cx("root", className)}>
            <h3 className={cx("title")}>{title}</h3>
            <div className={cx("body")}>
                {list.map((item, index) => (
                    <StepsListItem
                        {...item}
                        key={index}
                        className={cx("item")}
                    />
                ))}
            </div>
        </div>
    );
};
export default StepsList;
