import { FC, useEffect, useRef } from "react";
import classNames from "classnames/bind";

import { Questions } from "common/components/new/Questions";
import { Container } from "common/components/Container";
import { PriceBlock } from "common/components/new/PriceBlock";
import { NewAppointment } from "common/components/NewAppointment";
import { MaxWidthContainer } from "common/components/new/MaxWidthContainer";
import { useComputedFixedButtonOffset } from "common/hooks/useComputedFixedButtonOffset";
import { useFixedButtonContext } from "common/context/fixedButtonContext";
import { Heading } from "common/components/new/Heading";
import { CountryVersion } from "common/constants/enum";

import { Head } from "components/Head";
import { About } from "components/About";
import { Footer } from "components/Footer";
import { Advantages } from "components/Advantages";
import { StepsMediaQueryController } from "components/StepsMediaQueryController";
import { PatientFeedbackMediaQueryController } from "components/PatientFeedbackMediaQueryController";

import { AffiliateLayoutProps } from "./AffiliateLayout.types";
import styles from "./AffiliateLayout.module.scss";

const cx = classNames.bind(styles);

export const AffiliateLayout: FC<AffiliateLayoutProps> = ({
    content: {
        head,
        about,
        steps,
        prices,
        contacts,
        questions,
        advantages,
        appointment,
        patientFeedbacks,
    },
    version,
    language,
}) => {
    const appointmentRef = useRef<HTMLDivElement>(null);

    const {
        isFixedButtonActive,
        scrollToAppointment,
    } = useComputedFixedButtonOffset(appointmentRef);

    const { setFixedButton } = useFixedButtonContext();

    useEffect(() => {
        setFixedButton(isFixedButtonActive);
    }, [isFixedButtonActive]);

    const handleScrollToAppointment = () => {
        scrollToAppointment(true);
    };

    return (
        <MaxWidthContainer className={cx("root")}>
            <Head
                {...head}
                contacts={contacts}
                className={cx("head")}
                button={{
                    ...head.button,
                    onClick: handleScrollToAppointment,
                }}
                isFixedButtonActive={isFixedButtonActive}
            />
            <Container>
                <About className={cx("about")} {...about} />
                <Advantages {...advantages} className={cx("advantages")} />
                <StepsMediaQueryController
                    steps={steps}
                    className={cx("steps")}
                    onButtonClick={handleScrollToAppointment}
                />

                {prices && (
                    <PriceBlock
                        {...prices}
                        version={version}
                        className={cx("prices")}
                        onButtonClick={scrollToAppointment}
                    />
                )}
            </Container>
            <div ref={appointmentRef}>
                <Container>
                    <Heading
                        className={cx("appointment-title", {
                            "appointment-title_ss-pro":
                                version === CountryVersion.GERMANY,
                            "appointment-title_germany":
                                version === CountryVersion.GERMANY,
                        })}
                    >
                        {appointment.title}
                    </Heading>
                    <NewAppointment
                        version={version}
                        language={language}
                        className={cx("appointment")}
                    />
                </Container>
            </div>
            <Container>
                <PatientFeedbackMediaQueryController
                    className={cx("patient-feedback")}
                    {...patientFeedbacks}
                />
                <Questions {...questions} className={cx("question")} />
                <Footer {...contacts} />
            </Container>
        </MaxWidthContainer>
    );
};
