import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Badge } from "components/Badge";

import { StepsListItemProps } from "./StepsListItem.types";
import styles from "./StepsListItem.module.scss";

const cx = classNames.bind(styles);

const StepsListItem: FunctionComponent<StepsListItemProps> = (props) => {
    const { number, mobile, className } = props;

    return (
        <div
            className={cx("root", className)}
            style={{
                backgroundImage: `url(${mobile.image})`,
            }}
        >
            <div className={cx("gradient")}>
                <p className={cx("number")}>{number}</p>
                <p className={cx("title")}>{mobile.title}</p>
                <p className={cx("text")}>{mobile.text}</p>
                {mobile.special && (
                    <Badge className={cx("badge")}>{mobile.special}</Badge>
                )}
            </div>
        </div>
    );
};
export default StepsListItem;
