import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { TextTag, TextTheme, TextProps } from "./Text.types";

import styles from "./Text.module.scss";

const cx = classNames.bind(styles);

export const Text: FunctionComponent<TextProps> = (props) => {
    const {
        children,
        className,
        tag = TextTag.P,
        theme = TextTheme.Black,
    } = props;

    const classList = cx("root", className, `theme_${theme}`, `type_${tag}`);

    switch (tag) {
        case TextTag.H1:
            return <h1 className={classList}>{children}</h1>;
        case TextTag.H2:
            return <h2 className={classList}>{children}</h2>;
        case TextTag.H3:
            return <h3 className={classList}>{children}</h3>;
        case TextTag.H4:
            return <h4 className={classList}>{children}</h4>;
        case TextTag.H5:
            return <h5 className={classList}>{children}</h5>;
        case TextTag.H6:
            return <h6 className={classList}>{children}</h6>;
        case TextTag.Span:
            return <span className={classList}>{children}</span>;
        case TextTag.Div:
            return <div className={classList}>{children}</div>;
        case TextTag.P:
        default:
            return <p className={classList}>{children}</p>;
    }
};

export default Text;
