import { HTMLAttributes } from "react";

export interface TextProps extends HTMLAttributes<{}> {
    tag?: TextTag;
    theme?: TextTheme;
}

export enum TextTag {
    H1 = "h1",
    H2 = "h2",
    H3 = "h3",
    H4 = "h4",
    H5 = "h5",
    H6 = "h6",

    Span = "span",
    P = "p",
    Div = "div",
}

export enum TextTheme {
    Red = "red",
    White = "white",
    Black = "black",
}
